.projects-grid1{
  display: flex;
  flex-direction: column;
  align-content: space-between;
  justify-content: space-between;
}

.projects-grid2{
  margin-top: 5em;
  display: flex;
  flex-direction: column;
  align-content: space-between;
  justify-content: space-between;
}

@media(min-width:768px) {
  .projects-grid1 {
    flex-flow: row nowrap;
  }
  .projects-grid1>.left1 {
    order: 1;
  }
  .projects-grid1>.center1 {
    order: 2;
  }
  .projects-grid1>.right1 {
    order: 3;
  }
  .projects-grid2 {
    flex-flow: row nowrap;
  }
  .projects-grid2>.left2 {
    order: 1;
  }
  .projects-grid2>.center2 {
    order: 2;
  }
  .projects-grid2>.right2 {
    order: 3;
  }


}


