.slider{
  position: relative;
  margin-top: 8rem ;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;

}

.image{
  width: 700px;
  height: 400px;
  border-radius: 10px;

}

.right-arrow{
  position: absolute;
  top: 50%;
  right: 32px;
  font-size: 3rem;
  color: rgba(255,255,255,0.4);
  z-index: 10;
  cursor: pointer;
  user-select: none;
}

.left-arrow{
  position: absolute;
  top: 50%;
  left: 32px;
  font-size: 3rem;
  color: rgba(255,255,255,0.4);
  z-index: 10;
  cursor: pointer;
  user-select: none;
}

.right-arrow:hover{
  color: white;
}

.left-arrow:hover{
  color: white;
}

.slide{
  opacity: 0;
  transition-duration: 1s ease ;
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}


